import { Component } from '@angular/core';
import { AlertController, IonicPage, LoadingController, NavController, NavParams } from 'ionic-angular';
import { AnswerProvider } from "../../providers/answer/answer";
import { Answer, AnswerNeighborhood } from "../../providers/questionary/questionary";
import { Prioritization } from "../prioritization/prioritization";
import { UserType } from "../home/home";
import { Storage } from "@ionic/storage";

@IonicPage()
@Component({
  selector: 'page-thankyou',
  templateUrl: 'thankyou.html',
})
export class ThankyouPage {
  answers: Answer[];
  answersNeighborhoods: AnswerNeighborhood[];
  prioritizations: Prioritization[];
  loader: any;
  userType: UserType;
  progress: number;
  points: number;
  totalQuestions: number;

  constructor(public navCtrl: NavController, public navParams: NavParams, private answerProvider: AnswerProvider, public loadingCtrl: LoadingController, private alertCtrl: AlertController, private storage: Storage) {
    this.getUserType();
    this.points = navParams.get('points');
    this.answers = navParams.get('answers');
    this.answersNeighborhoods = navParams.get('answersNeighborhoods');
    this.prioritizations = navParams.get('prioritizations');
    this.progress = 100;
    let questions = navParams.get('questions');
    this.totalQuestions = questions.length;
  }

  help() {
    let alert = this.alertCtrl.create({
      title: '<div text-center>Sua participação não acaba aqui!</div>',
      message:
        '<div class="alert-align-center"><img class="img-alert" src="assets/imgs/publicdomainq-presenter.png"/></div>'
        + '<div text-center>Ainda não acabou!</div>'
        + '<div text-center>Você pode responder outros questionários e continuar contribuindo!</div>',
      buttons: [{
        text: "Ok!",
        handler: () => {
        }
      }]
    });
    alert.present();
  }

  getUserType() {
    this.storage.get('userType')
      .then((data: UserType) => {
        if (data != null) {
          this.userType = data;
        } else {
          let userType = new UserType();
          userType.id = null;
          userType.name = null;
          this.userType = userType;
        }
      })
      .catch(() => {
        let userType = new UserType();
        userType.id = null;
        userType.name = null;
        this.userType = userType;
      });
  }

  finishQuestionary() {
    this.loader = this.loadingCtrl.create();
    this.loader.present();
    this.answerProvider.insertAnswersData(this.answers, this.answersNeighborhoods, this.prioritizations, this.userType)
      .then((data: boolean) => {
        if (data != null) {
          this.storage.set('points', this.points);
          this.navCtrl.setRoot('QuestionariesListPage', {}).then(this.loader.dismiss());
        } else {
          let alert = this.alertCtrl.create({
            title: 'Oops!',
            message: 'Não foi possível enviar os dados para o servidor. Por favor, tente novamente.',
            buttons: [{
              text: "Tentar novamente",
              handler: () => {
                this.finishQuestionary();
              }
            }]
          });
          alert.present();
          this.loader.dismiss();
        }
      })
      .catch(() => {
        let alert = this.alertCtrl.create({
          title: 'Oops!',
          message: 'Não foi possível enviar os dados para o servidor. Por favor, tente novamente.',
          buttons: [{
            text: "Tentar novamente",
            handler: () => {
              this.finishQuestionary();
            }
          }]
        });
        alert.present();
        this.loader.dismiss();
      });
  }
}



